<template>
  <div class="procurement">
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--primary custom-button--right"
      @click="toggleDialog"
    >
      <i class="material-icons">add</i> Add
    </button>
    <transition name="component-fade" mode="out-in">
      <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone" v-show="!showEditView">
        <data-table
          :columns="columns"
          :data="procurements.data"
          :actionMethod="fetchProcurements"
          :count="procurements.count"
          :searchPlaceholder="'Search by Procurement Number'"
          @rowSelected="currentProcurement=$event"
        ></data-table>
      </div>
    </transition>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone">
      <dialog-view ref="addFormDialog">
        <div slot="dialog-body">
          <procurement-form-view ref="procurementAddForm" :afterSuccess="toggleDialog"></procurement-form-view>
        </div>
        <div slot="dialog-actions">
          <button
            class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
            @click="proceed"
          >
          Create
          </button>
        </div>
      </dialog-view>
    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone" v-show="showEditView">
      <button
        class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--primary"
        @click="showEditView=!showEditView"
      >
      Back
      </button>
      <procurement-edit-form-view ref="procurementEditForm" :procurement="currentProcurement"></procurement-edit-form-view>
      <!-- <dialog-view ref="editFormDialog" :displayCancleButton="false">
        <div slot="dialog-body">
        </div>
        <div slot="dialog-actions">
        </div>
      </dialog-view> -->
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { DialogView } from 'SHARED/dialog'
import { ProcurementFormView, ProcurementEditFormView } from '.'

export default {
  name: 'procurements-list-view',
  components: {
    DataTable,
    DialogView,
    ProcurementFormView,
    ProcurementEditFormView
  },
  computed: {
    ...mapGetters({
      procurements: 'getProcurements'
    }),
    currentProcurement: {
      get () {
        return this.procurement
      },
      set (procurement) {
        this.procurement = procurement
        this.showEditView = true
        // this.toggleEditDialog(true)
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchProcurements'
    ]),
    toggleDialog (show) {
      if (show) {
        this.$refs.addFormDialog.showModal()
      } else {
        this.$refs.addFormDialog.closeModal()
      }
    },
    toggleEditDialog (show) {
      if (show) {
        this.$refs.editFormDialog.showModal()
      } else {
        this.$refs.editFormDialog.closeModal()
      }
    },
    proceed () {
      console.log(this.$refs.procurementAddForm.proceed())
    }
  },
  mounted () {
    this.fetchProcurements()
  },
  data () {
    return {
      columns: [
        {
          field: 'procurement_number',
          label: 'Procurement Number',
          type: String
        },
        {
          field: 'status',
          label: 'Status',
          type: String
        }
      ],
      procurement: {},
      showEditView: false
    }
  }
}
</script>



